<template>
  <div>
    <!-- <b-card-group deck> -->
      <div v-for="post in posts" :key="post.postId">
      <b-card header-tag="header" footer-tag="footer"> <!-- title="Title"     -->
      <!-- <div v-for="post in posts" v-bind:key="post.postId" @click="detail(post.postId)"> 
        <p>{{post.nickname}}</p>
        <br>
        <p>{{post.postContent}}</p>
      </div> -->
        <template>
          <b-card-text class="font-weight-bold">
            <!--    backend sql     -->
<!-- ,    .      !!!-->
            <!-- <span class="mr-5"> img</span> -->
            <span >{{post.nickname}}</span>
          </b-card-text>
        </template>
        <div @click="detail(post)"> <!-- for  -->
          <!-- <b-card-img class="col-3 mb-5" src="https://placekitten.com/480/210" alt="Image" bottom></b-card-img> -->
          <ImageSlick />
          <p>{{post.postContent}}</p>
        </div>
        <template #footer>
          <b-icon icon="suit-heart" variant="danger"></b-icon>
          <b-icon icon="suit-heart-fill" variant="danger"></b-icon>
          <b-icon icon="chat"></b-icon>
          <!-- <b-button class="mr-5" href="#" variant="info"></b-button> -->
          <b-button class="ml-5" href="#" variant="info"></b-button>
        </template>
      </b-card>
    <!-- </b-card-group> -->
      </div>
   <div>
</div>   
  </div>
</template>

<script>
import ImageSlick from '@/components/story/ImageSlick'
import { mapGetters } from "vuex";
import axios from 'axios';
const SERVER_URL = "http://localhost:8000";

export default {
  name: 'PostBlockMy',
  components: {
    ImageSlick,
  },
  computed: {
    ...mapGetters(["getUserId"])
  },
  data() {
    return {
      posts:[],
      limit: 5,  //    
      offset: 0,  //  
    }
  },
  mounted() {
    this.getPostInfos();
  },
  methods: {
    getPostInfos(){
      axios
        .get(`${SERVER_URL}/userpost/user`, {
          params: {
            userId: this.getUserId,
            limit: this.limit,
            offset: this.offset
          }
      })
        .then((response) => {
          this.posts = response.data.list;
          console.log(this.posts);
        });
    },
    detail(post){
      this.$router.push({ name: "ArticleDetail", params: { postInfo: post} });
    }
  },
}
</script>

<style>
  
  /*  */
  b-card-img {
    height: 5px;
    width: 5px;
  }
</style>